import './App.css';
import SearchPanel from './components/search-panel';
import InfoPanel from './components/info-panel';
import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';

function App() {
	const [shouldInfo, setShouldInfo] = useState(false);

	const iconCountry = useSelector((state) => state.weather.data);
	console.log(iconCountry);
	useEffect(() => {
		document.addEventListener('keyup', (e) => {
			if (e.key === 'Escape') {
				setShouldInfo(false);
			}
		});
	}, [shouldInfo]);

	return (
		<div className='App'>
			<SearchPanel setShouldInfo={setShouldInfo} />
			<InfoPanel setShouldInfo={setShouldInfo} shouldInfo={shouldInfo} iconCountry={iconCountry.sys?.country} />
		</div>
	);
}

export default App;
