import React from 'react';
import styles from './style.module.scss';
import { useSelector } from 'react-redux';
import { Container } from '../../helper/wrapper';

const InfoPanel = ({ setShouldInfo, shouldInfo, iconCountry }) => {
	const data = useSelector((state) => state.weather.data);
	const error = useSelector((state) => state.weather);
	const date = new Date();
	const dateHours = date.getHours();

	return (
		<div className={`${styles.box} ${shouldInfo && styles.show}`}>
			<span className={styles.exit} onClick={() => setShouldInfo(false)}>
				X
			</span>
			<Container>
				<div className={`${error.errorMessage ? styles.hidden : styles.boxItems}`}>
					<img src={`https://www.countryflagicons.com/FLAT/64/${iconCountry}.png`} alt='' />
					<p className={styles.city}>{data.name}</p>
					<p className={styles.temp}>{Math.floor(data.main?.temp)} &#8451; </p>
					<p className={styles.sky}>{data.weather && data.weather[0].description}</p>
					<div className={styles.wind}>
						<span>wind</span>
						<span>|</span>
						<span>{data.wind?.speed} km/h</span>
					</div>
				</div>

				<div className={styles.error}>{error.errorMessage !== null ? error.errorMessage : ''}</div>
			</Container>
		</div>
	);
};
export default InfoPanel;
